import { immerable } from "immer";
import { generate } from "shortid";
import { Region, RegionGridType } from "./Editor";
import { DeepPartial } from "ts-essentials";
import { buildPoint } from "./Point";
import { merge } from "lodash";

const SEPARATOR = "__";
export const ID_REGION = "region";
export enum RegionChildSuffix {
  BG = "bg",
  GRID_CELL = "gridCell",
  CONTENT = "content",
}

export const getThreeNameFromRegionId = (
  id: string,
  childSuffix?: RegionChildSuffix
) => {
  return `${ID_REGION}${SEPARATOR}${id}${SEPARATOR}${childSuffix}`;
};

export const getRegionIdFromThreeName = (name?: string) => {
  if (!name) {
    return undefined;
  }
  if (name.includes(ID_REGION) === false) {
    return undefined;
  }
  return name.split(SEPARATOR)[1];
};

export const buildRegion = (props: DeepPartial<Region>): Region => {
  const defaults: Region = {
    id: generate(),
    position: buildPoint(0, 0),
    gridType: RegionGridType.Rectangles,
    height: 10,
    width: 10,
    beadIds: [],
    opacity: 0.5,
    settings: {
      columns: 3,
      rows: 3,
    },
    [immerable]: true,
  };

  return merge(defaults, props);
};
