  action_resetSelectedLines() {
    const firstLineId = Object.keys(this.draftState.ui.selectedLinesIds)[0];
    const firstLine = this.draftState.appData.lines[firstLineId];
    const defaultBead = { ...firstLine.settings.defaultBead };

    defaultBead.id = undefined;
    defaultBead.position = undefined;

    Object.keys(this.draftState.ui.selectedLinesIds).forEach((id) => {
      this.draftState.appData.lines[id].beadIds.forEach((beadId) => {
        if (defaultBead) {
          this.draftState.appData.beads[beadId] = merge(
            this.draftState.appData.beads[beadId],
            defaultBead
          );
        }
      });
      this.draftState.appData.lines[id].settings.defaultBead = merge(
        this.draftState.appData.lines[id].settings.defaultBead,
        defaultBead
      );
      this.updateBeadsPositions(id);
    });
    this.editorAppDataHasChanged();
  }

  action_deleteSelectedLines() {
    Object.keys(this.draftState.ui.selectedLinesIds).forEach((id) => {
      this.draftState.appData.lines[id].beadIds.forEach((beadId) => {
        delete this.draftState.appData.beads[beadId];
      });
      delete this.draftState.appData.lines[id];
    });
    this.draftState.ui.selectedLinesIds = {};
    this.draftState.ui.selectedLinesIdsCount = 0;
    this.editorAppDataHasChanged();
  }

  action_cloneSelectedLines() {
    const moveX = 20;
    const moveY = 20;
    const newLinesIds: string[] = [];

    Object.keys(this.draftState.ui.selectedLinesIds).forEach((id) => {
      const newLine = buildLine({
        ...this.draftState.appData.lines[id],
        id: undefined,
        beadIds: [],
      });
      newLine.start.x = newLine.start.x + moveX;
      newLine.start.y = newLine.start.y + moveY;
      newLine.end.x = newLine.end.x + moveX;
      newLine.end.y = newLine.end.y + moveY;

      this.draftState.appData.lines[id].beadIds.forEach((beadId) => {
        const newBead = buildBead({
          ...this.draftState.appData.beads[beadId],
          id: undefined,
        });
        newBead.position.x = newBead.position.x + moveX;
        newBead.position.y = newBead.position.y + moveY;
        this.draftState.appData.beads[newBead.id] = newBead;
        newLine.beadIds.push(newBead.id);
      });

      newLinesIds.push(newLine.id);
      this.draftState.appData.lines[newLine.id] = newLine;
    });

    newLinesIds.forEach((lineId) => {
      this.updateBeadsPositions(lineId);
    });

    this.draftState.ui.selectedLinesIds = {};
    this.draftState.ui.selectedLinesIdsCount = 0;

    newLinesIds.forEach((lineId) => {
      this.draftState.ui.selectedLinesIds[lineId] = true;
      this.draftState.ui.selectedLinesIdsCount =
        this.draftState.ui.selectedLinesIdsCount + 1;
    });
    this.editorAppDataHasChanged();
  }

