import { times } from "lodash";
import * as THREE from "three";
import { BeadChildSuffix, getThreeNameFromBeadId } from "../editorObjects/Bead";
import { Bead, BeadTypes, Editor, Region } from "../editorObjects/Editor";
import { getThreeNameFromRegionId, RegionChildSuffix } from "../editorObjects/Region";
import { EventNames, getEventEmitter } from "../EventEmitter";
import { Base3dModels } from "./renderThreejs";

export const initEventEmitterHandlers = (
  scene: THREE.Scene,
  base3dModels: Base3dModels
) => {
  const reflectivity = 1,
    eventEmitter = getEventEmitter();

  eventEmitter.on(EventNames.action_changeSelectedBeads_color, (data) => {
    data.beadIds.forEach((id) => {
      const object = scene.getObjectByName(getThreeNameFromBeadId(id)) as
        | THREE.Mesh
        | undefined;
      if (object === undefined) {
        return;
      }

      object.traverse((child) => {
        if (child instanceof THREE.Mesh) {
          if (child.material) {
            (child.material as THREE.MeshPhysicalMaterial).color.set(
              data.color
            );
          }
        }
      });
    });
  });

  eventEmitter.on(EventNames.action_changeSelectedBeads_height, (data) => {
    data.beads.forEach((bead: Bead) => {
      const object = scene.getObjectByName(getThreeNameFromBeadId(bead.id)) as
        | THREE.Mesh
        | undefined;
      if (object === undefined) {
        return;
      }
      object.scale.x = 3 * (bead.height * 0.15);
      object.scale.y = 3 * (bead.depth * 0.15);
      object.scale.z = 3 * (bead.width * 0.15);
    });
  });

  eventEmitter.on(EventNames.action_changeSelectedBeads_width, (data) => {
    data.beads.forEach((bead: Bead) => {
      const object = scene.getObjectByName(getThreeNameFromBeadId(bead.id)) as
        | THREE.Mesh
        | undefined;
      if (object === undefined) {
        return;
      }
      object.scale.x = 3 * (bead.height * 0.15);
      object.scale.y = 3 * (bead.depth * 0.15);
      object.scale.z = 3 * (bead.width * 0.15);
    });
  });

  eventEmitter.on(EventNames.action_changeSelectedBeads_depth, (data) => {
    data.beads.forEach((bead: Bead) => {
      const object = scene.getObjectByName(getThreeNameFromBeadId(bead.id)) as
        | THREE.Mesh
        | undefined;
      if (object === undefined) {
        return;
      }
      object.scale.x = 3 * (bead.height * 0.15);
      object.scale.y = 3 * (bead.depth * 0.15);
      object.scale.z = 3 * (bead.width * 0.15);
    });
  });

  eventEmitter.on(EventNames.action_changeSelectedBeads_type, (data) => {
    data.beadIds.forEach((id) => {
      const object = scene.getObjectByName(getThreeNameFromBeadId(id)) as
        | THREE.Mesh
        | undefined;
      if (object === undefined) {
        return;
      }
      scene.remove(object);
    });
    data.beads.forEach((bead: Bead) => {
      triggerAddBeadToScene(bead);
      triggerSelectBead(bead.id, true);
    });
  });

  eventEmitter.on(EventNames.action_changeSelectedBeads_rotate, (data) => {
    data.beadIds.forEach((id) => {
      const object = scene.getObjectByName(getThreeNameFromBeadId(id)) as
        | THREE.Mesh
        | undefined;
      if (object === undefined) {
        return;
      }

      object.rotation.x = THREE.MathUtils.DEG2RAD * data.rotateX;
      object.rotation.y = THREE.MathUtils.DEG2RAD * data.rotateY;
      object.rotation.z = THREE.MathUtils.DEG2RAD * data.rotateZ;
    });
  });

  eventEmitter.on(EventNames.action_selectBeads, (data) => {
    data.diffStates.forEach((bead) => {
      const id = Object.keys(bead)[0];
      const value = Object.values(bead)[0] as boolean;
      triggerSelectBead(id, value);
    });
  });

  const triggerSelectBead = (id: string, shouldSelect: boolean) => {
    const object = scene.getObjectByName(getThreeNameFromBeadId(id)) as
      | THREE.Mesh
      | undefined;
    if (object === undefined) {
      return;
    }

    object.traverse((child) => {
      if (child instanceof THREE.Mesh) {
        if (child.material) {
          if (shouldSelect === true) {
            (child.material as THREE.MeshPhysicalMaterial).emissive.set(
              0x171717
            );
          } else {
            (child.material as THREE.MeshPhysicalMaterial).emissive.set(0);
          }
        }
      }
    });
  };

  eventEmitter.on(EventNames.action_deleteBeads, (data) => {
    triggerDeleteBeadIds(data.beadIds);
  });

  eventEmitter.on(EventNames.action_deleteSelectedBeads, (data) => {
    triggerDeleteBeadIds(data.beadIds);
  });

  const triggerDeleteBeadIds = (beadIds: string[]) => {
    beadIds.forEach((beadId) => {
      while (true) {
        const object = scene.getObjectByName(getThreeNameFromBeadId(beadId));
        if (object === undefined) {
          break;
        }
        scene.remove(object);
      }
      while (true) {
        const object = scene.getObjectByName(
          getThreeNameFromBeadId(beadId, BeadChildSuffix.ANY)
        );
        if (object === undefined) {
          break;
        }
        scene.remove(object);
      }
    });
  };

  eventEmitter.on(EventNames.action_addBeadOnClick, (data) => {
    const bead = data.bead as Bead;
    triggerAddBeadToScene(bead);
  });

  eventEmitter.on(
    EventNames.action_updateLight,
    (data: { name; field; value }) => {
      const light = scene.getObjectByName(data.name) as
        | THREE.DirectionalLight
        | undefined;
      if (light !== undefined) {
        if (data.field === "color") {
          light.color = data.value;
        }
        if (data.field === "intensity") {
          light.intensity = data.value;
        }
      }
    }
  );

  eventEmitter.on(EventNames.action_addRegion, (data) => {
    const region = data.region as Region;

    const geometry = new THREE.PlaneGeometry(1, 1, 1, 1);
    // @TODO: zmiana koloru i opacity
    const material = new THREE.MeshBasicMaterial({
      color: 0xff0000,
      side: THREE.FrontSide,
      opacity: 0.1,
      transparent: true,
    });
    const regionBgThree = new THREE.Mesh(geometry, material);

    regionBgThree.name = getThreeNameFromRegionId(region.id, RegionChildSuffix.BG);

    const posStartX = region.position.x + region.width / 2;
    const posStartY = region.position.y + region.height / 2;

    const regionGridThree = new THREE.Group();

    times(region.settings.rows).forEach((row, i) => {
      times(region.settings.columns).forEach((column, j) => {
        const width = 1 / region.settings.columns;
        const height = 1 / region.settings.rows;
        const rectWidth = width - 0.0;
        const rectHeight = height - 0.0;
        const rectShape = new THREE.Shape()
          .moveTo(rectWidth / 2, rectHeight / 2)
          .lineTo((rectWidth / 2) * -1, rectHeight / 2)
          .lineTo((rectWidth / 2) * -1, (rectHeight / 2) * -1)
          .lineTo(rectWidth / 2, (rectHeight / 2) * -1);

        rectShape.autoClose = true;

        const _rectPoints = rectShape.getPoints();
        const _rectGeometryPoints = new THREE.BufferGeometry().setFromPoints(
          _rectPoints
        );
        const _lineMaterial = new THREE.MeshBasicMaterial({ color: 0x0040f0 });
        const _cell = new THREE.Line(_rectGeometryPoints, _lineMaterial);
        _cell.name = getThreeNameFromRegionId(region.id, RegionChildSuffix.GRID_CELL);
        _cell.position.x = -1 * 0.5 + column * width + width / 2;
        _cell.position.y = -1 * 0.5 + row * height + height / 2;

        regionGridThree.add(_cell);
      });
    });

    const regionThree = new THREE.Group();
    regionThree.name = getThreeNameFromRegionId(region.id);

    const regionContentThree = new THREE.Group();
    regionContentThree.name = getThreeNameFromRegionId(
      region.id,
      RegionChildSuffix.CONTENT
    );
    regionContentThree.position.x = posStartX;
    regionContentThree.position.y = posStartY;
    regionContentThree.position.z = -500;
    regionContentThree.scale.x = region.width;
    regionContentThree.scale.y = region.height;
    regionContentThree.scale.z = 1;
    regionContentThree.add(regionBgThree);
    regionContentThree.add(regionGridThree);

    regionThree.add(regionContentThree);

    scene.add(regionThree);

    // @TODO: CHeck lines
    // const material = new THREE.LineBasicMaterial({ color: 0x0000ff });
    // material.linewidth = 10
    // const points: THREE.Vector3[] = [];
    // points.push(new THREE.Vector3(-10, 0, 0));
    // points.push(new THREE.Vector3(0, 10, 0));
    // points.push(new THREE.Vector3(10, 0, 0));
    // const geometry = new THREE.BufferGeometry().setFromPoints(points);
    // const line = new THREE.Line(geometry, material);
    // scene.add(line)
  });

  const triggerAddBeadToScene = (bead: Bead) => {
    let beadModel: THREE.Object3D | undefined;
    // @TODO: USTAWIC W MODELU punkt (0,0,0)
    if (bead.type === BeadTypes.BasicRectangle) {
      if (base3dModels.diamond === null) {
        console.error("triggerAddBeadToScene: 3dModel is not loaded diamond");
        return;
      }
      beadModel = base3dModels.diamond.clone();
    }
    if (bead.type === BeadTypes.BasicEllipse) {
      if (base3dModels.modelToho15v3a === null) {
        console.error("triggerAddBeadToScene: 3dModel is not loaded baseBead");
        return;
      }
      beadModel = base3dModels.modelToho15v3a.clone();
    }

    if (beadModel === undefined) {
      return;
    }

    beadModel.traverse((child) => {
      child.name = getThreeNameFromBeadId(bead.id, BeadChildSuffix.ANY);
      if (child instanceof THREE.Mesh) {
        child.material = new THREE.MeshPhysicalMaterial({
          flatShading: true,
          reflectivity: reflectivity,
          color: bead.color,
        });
      }
      if ((child as any).geometry) {
        (child as any).geometry.computeBoundingBox();
      }
    });

    beadModel.name = getThreeNameFromBeadId(bead.id);

    // beadModel.scale.x = 3 * (bead.height * 0.15);
    // beadModel.scale.y = 3 * (bead.depth * 0.15);
    // beadModel.scale.z = 3 * (bead.width * 0.15);

    beadModel.rotation.x = THREE.MathUtils.DEG2RAD * bead.rotateX;
    beadModel.rotation.y = THREE.MathUtils.DEG2RAD * bead.rotateY;
    beadModel.rotation.z = THREE.MathUtils.DEG2RAD * bead.rotateZ;

    beadModel.position.x = bead.position.x;
    beadModel.position.y = bead.position.y;

    scene.add(beadModel);
    if (beadModel) {
    }
  };

  eventEmitter.on(EventNames.action_movedSelectedBeads, (data) => {
    data.beads.forEach((bead: Bead) => {
      const object = scene.getObjectByName(getThreeNameFromBeadId(bead.id));
      if (object === undefined) {
        return;
      }
      object.position.x = bead.position.x;
      object.position.y = bead.position.y;
    });
  });

  eventEmitter.on(EventNames.action_changeBeadColorToBeadTemplate, (data) => {
    data.beads.forEach((bead: Bead) => {
      const object = scene.getObjectByName(getThreeNameFromBeadId(bead.id));
      if (object === undefined) {
        return;
      }
      object.traverse((child) => {
        if (child instanceof THREE.Mesh) {
          child.material = new THREE.MeshPhysicalMaterial({
            flatShading: true,
            reflectivity: reflectivity,
            color: data.color,
          });
        }
      });
    });
  });

  eventEmitter.on(EventNames.action_fastAddBeads, (data) => {
    Object.keys(data.beads).forEach((id) => {
      const bead = data.beads[id] as Bead;
      triggerAddBeadToScene(bead);
    });

    data.prevSelectedBeadIds.forEach((beadId) => {
      triggerSelectBead(beadId, false);
    });
  });

  eventEmitter.on(EventNames.action_snapToLineSelectedBeads, (data) => {
    data.beads.forEach((bead: Bead) => {
      const object = scene.getObjectByName(getThreeNameFromBeadId(bead.id));
      if (object === undefined) {
        return;
      }
      object.position.x = bead.position.x;
      object.position.y = bead.position.y;
    });
  });

  eventEmitter.on(EventNames.action_onChangeEditorMode, (data) => {
    data.prevSelectedBeadIds.forEach((beadId) => {
      triggerSelectBead(beadId, false);
    });
  });

  eventEmitter.on(EventNames.action_readSavedCanvas, (data) => {
    Object.keys(data.beads).forEach((id) => {
      const bead = data.beads[id] as Bead;
      triggerAddBeadToScene(bead);
    });
  });

  eventEmitter.on(EventNames.startAppEvent, (data: { state: Editor }) => {
    console.log("startAppEvent: ...", data.state.ui.lights);
    if (data.state.ui.lights && data.state.ui.lights.length > 0) {
      data.state.ui.lights.forEach((l) => {
        if (l.type === "THREE.DirectionalLight") {
          const light = new THREE.DirectionalLight(l.color, l.intensity);
          light.position.set(
            l.position?.x ?? 0,
            l.position?.y ?? 0,
            l.position?.z ?? 10
          );
          light.name = l.name;
          scene.add(light);
        }
      });
    }
  });
};
