import { immerable } from "immer";
import { DeepPartial } from "ts-essentials";

export interface Point {
  x: number;
  y: number;
  [immerable]: boolean;
}

export enum BeadTypes {
  BasicRectangle = "BasicRectangle",
  BasicEllipse = "BasicEllipse",
}

export interface Bead {
  id: string;
  position: Point;
  [immerable]: boolean;
  width: number;
  height: number;
  depth: number;
  type: BeadTypes;
  color: string;
  // rotate: number;
  rotateX: number;
  rotateY: number;
  rotateZ: number;
  lineRotate: number;
  keepAspectRatio: boolean;
}

export interface Line {
  id: string;
  start: Point;
  end: Point;
  beadIds: string[];
  [immerable]: boolean;
  settings: {
    beadsCount: number;
    beadsAutoCount: boolean;
    beadsSpacing: number;
    beadsAutoSpacing: boolean;
    defaultBead: Partial<Bead>;
  };
}

export enum RegionGridType {
  Rectangles = "Rectangles",
}

export interface Region {
  id: string;
  position: Point;
  width: number;
  height: number;
  beadIds: string[];
  gridType: RegionGridType;
  opacity: number;
  [immerable]: boolean;
  settings: {
    columns: number;
    rows: number;
    // beadsCount: number;
    // beadsAutoCount: boolean;
    // beadsSpacing: number;
    // beadsAutoSpacing: boolean;
    // defaultBead: Partial<Bead>;
  };
}

export interface AppData {
  beads: { [s: string]: Bead };
  lines: { [s: string]: Line };
  regions: { [s: string]: Region };
}

export enum EditorMode {
  Idle = "Idle",
  EditLines = "EditLines",
  AddLine = "AddLine",
  EditBeads = "EditBeads",
  AddBeads = "AddBeads",
  DeleteBeads = "DeleteBeads",
  PaintBeads = "ColorPaint",
  AddRegion = "AddRegion",
}
export enum EditorMenuMain {
  None = "None",
  File = "File",
  Grid = "Grid",
  Background = "Background",
}
export enum EditorGridType {
  Hex = "Hex",
  Box = "Box",
}
export enum BrushType {
  Color = "Color",
}
export interface ThreeLightConfig {
  type: "THREE.DirectionalLight" | "THREE.AmbientLight";
  name: string;
  color?: string | number | THREE.Color | undefined;
  intensity?: number | undefined;
  position?: {
    x: number;
    y: number;
    z: number;
  };
}

export interface EditorUi {
  lights: ThreeLightConfig[];
  lineSettings: DeepPartial<Line>;
  saveInfo: {
    saved: boolean;
    haveUnsavedChanges: boolean;
  };
  selectedLinesIds: { [s: string]: boolean };
  selectedLinesIdsCount: number;
  selectedBeadsIds: { [s: string]: boolean };
  selectedBeadsIdsCount: number;
  mouseSelection: {
    start: Point;
    end: Point;
    isDragStarted: boolean;
    isDragged: boolean;
  };
  currentMenuMainOpen: EditorMenuMain;
  grid: {
    type: EditorGridType;
    hexSize: number;
    snapEnabled: boolean;
    isVisible: boolean;
    width: number;
    height: number;
  };
  currentBeadChain: {
    isDragEnabled: boolean;
    beadIds: string[];
  };
  beadsTemplate: {
    width: number;
    height: number;
    depth: number;
    type: BeadTypes;
    color: string;
    rotateX: number;
    rotateY: number;
    rotateZ: number;
    keepAspectRatio: boolean;
  };
  regionTemplate: {
    gridType: RegionGridType;
  };
  brush: {
    type: BrushType;
    size: number;
    color: string;
  };
  background: {
    image: {
      url: string;
      scale: number;
      position: Point;
      opacity: number;
    };
  };
  sceneWrapperWidth: number;
  sceneWrapperHeight: number;
  sceneWrapperPosition: {
    x: number;
    y: number;
    angle: number;
    scale: number;
    top: number;
    left: number;
  };
}

export interface Editor {
  appData: AppData;
  ui: EditorUi;
  mode: EditorMode;
}
