const Lights: React.FC<{}> = (props) => {
  const dispatch = useDispatch();
  const lights = useSelector((state: Editor) => {
    return state.ui.lights;
  });
  return (
    <LightsStyles>
      <div className={"toolbar-lights"}>
        {lights.map((l, index) => {
          const onChange = (event: React.ChangeEvent<HTMLInputElement>) => {
            const newValue = Number(event.target.value) / 100;
            console.log("co mam?", newValue);
            dispatch(
              Actions.Toolbar.action_updateLight(index, "intensity", newValue)
            );
          };
          return (
            <div key={l.name} className={"light"}>
              <div className={"info"}>{JSON.stringify(l)}</div>
              <div className={"fields"}>
                <fieldset>
                  <label>intensity: </label>
                  <input
                    type="range"
                    name={`${l.name}_${l.intensity}`}
                    value={(l.intensity ?? 0) * 100}
                    onChange={onChange}
                    min="1"
                    max="99"
                    step="1"
                  />
                </fieldset>
              </div>
            </div>
          );
        })}
      </div>
    </LightsStyles>
  );
};
