import { Editor, EditorMenuMain, Point, Region } from "../editorObjects/Editor";
import { ImmerReducer } from "immer-reducer";
import { buildPoint } from "../editorObjects/Point";
import { buildRegion } from "../editorObjects/Region";
import { EventNames, getEventEmitter } from "../EventEmitter";

export class RegionImmerReducer extends ImmerReducer<Editor> {
  action_addRegion(startPoint: Point, endPoint: Point) {
    const minX = Math.min(startPoint.x, endPoint.x);
    const minY = Math.min(startPoint.y, endPoint.y);
    const width = Math.abs(endPoint.x - startPoint.x);
    const height = Math.abs(endPoint.y - startPoint.y);
    let defaultRegion: Partial<Region> = {
      ...this.draftState.ui.regionTemplate,
    };
    defaultRegion.id = undefined;
    defaultRegion.position = buildPoint(minX, minY);
    defaultRegion.height = height;
    defaultRegion.width = width;
    const region = buildRegion(defaultRegion);
    console.log("chce dodac", startPoint, endPoint, "dodano", region);
    this.draftState.appData.regions[region.id] = region;
    getEventEmitter().emit(
      EventNames.action_addRegion,
      {
        region,
      },
      this.draftState
    );
  }
}
