import { Editor, EditorMode } from "../editorObjects/Editor";
import { ImmerReducer } from "immer-reducer";
import { EventNames, getEventEmitter } from "../EventEmitter";

export class ChangeEditorModeImmerReducer extends ImmerReducer<Editor> {
  action_onChangeEditorMode(newEditorMode: EditorMode) {
    const prevSelectedLinesIds = Object.keys(
      this.draftState.ui.selectedLinesIds
    );
    const prevSelectedBeadIds = Object.keys(
      this.draftState.ui.selectedBeadsIds
    );
    this.draftState.mode = newEditorMode;
    this.clearSelectedBeads();
    this.clearSelectedLines();
    getEventEmitter().emit(
      EventNames.action_onChangeEditorMode,
      {
        prevSelectedBeadIds,
        prevSelectedLinesIds,
      },
      this.state
    );
  }
  private clearSelectedBeads = () => {
    if (this.draftState.ui.selectedBeadsIdsCount > 0) {
      this.draftState.ui.selectedBeadsIds = {};
      this.draftState.ui.selectedBeadsIdsCount = 0;
    }
  };
  private clearSelectedLines = () => {
    if (this.draftState.ui.selectedLinesIdsCount) {
      this.draftState.ui.selectedLinesIds = {};
      this.draftState.ui.selectedLinesIdsCount = 0;
    }
  };
  action_setSaveInfo(newValue: boolean) {
    this.draftState.ui.saveInfo.saved = newValue;
  }
}
