import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { Editor } from "../../../editorObjects/Editor";
import { FieldBeadPreview } from "../ui/FieldBeadPreview";
import { Actions } from "../../../immerReducers/store";
import { FieldsetColor } from "../ui/FieldsetColor";
import { FieldsetBeadType } from "../ui/FieldsetBeadType";
// import { FieldsetBeadSpacing } from "../ui/FieldsetBeadSpacing";
// import { FieldsetBeadCount } from "../ui/FieldsetBeadCount";
import { FieldsetWidthAndHeight } from "../ui/FieldsetWidthAndHeight";
// import { FieldDeleteAndCopy } from "../ui/FieldDeleteAndCopy";
import { FieldsetBeadRotate } from "../ui/FieldsetBeadRotate";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAlignCenter,
  faAlignLeft,
  faAlignRight,
  faTimes,
} from "@fortawesome/free-solid-svg-icons";
import { uiButton } from "../../colors";
import { SnapToLineType } from "../../../immerReducers/ToolbarImmerReducer";

export const ToolbarEditBeads = ({ isVisible }) => {
  if (isVisible === true) {
    return <ContentWrapper />;
  }

  return <React.Fragment />;
};

const ContentWrapper = () => {
  const showToolbar = useSelector((state: Editor) => {
    return state.ui.selectedBeadsIdsCount >= 1;
  });
  if (showToolbar) {
    return <Content />;
  }
  return <React.Fragment />;
};

const Content = () => {
  return (
    <React.Fragment>
      <BeadPreview />
      <BeadSize />
      <BeadColor />
      <BeadType />
      <BeadsRotate />
      <BeadsSnapTo />
    </React.Fragment>
  );
};

const BeadsSnapTo = () => {
  const dispatch = useDispatch();
  const selected = useSelector((state: Editor) => {
    return state.ui.selectedBeadsIdsCount;
  });
  if (selected <= 1) {
    return <React.Fragment />;
  }
  const onClickAlighXLeft = () => {
    dispatch(
      Actions.Toolbar.action_snapToLineSelectedBeads(SnapToLineType.X_MIN)
    );
  };
  const onClickAlighXMiddle = () => {
    dispatch(
      Actions.Toolbar.action_snapToLineSelectedBeads(SnapToLineType.X_MIDDLE)
    );
  };
  const onClickAlighXRight = () => {
    dispatch(
      Actions.Toolbar.action_snapToLineSelectedBeads(SnapToLineType.X_MAX)
    );
  };
  const onClickAlighYLeft = () => {
    dispatch(
      Actions.Toolbar.action_snapToLineSelectedBeads(SnapToLineType.Y_MIN)
    );
  };
  const onClickAlighYMiddle = () => {
    dispatch(
      Actions.Toolbar.action_snapToLineSelectedBeads(SnapToLineType.Y_MIDDLE)
    );
  };
  const onClickAlighYRight = () => {
    dispatch(
      Actions.Toolbar.action_snapToLineSelectedBeads(SnapToLineType.Y_MAX)
    );
  };
  

  const onClickDeleteSelected = () => {
    dispatch(Actions.Toolbar.action_deleteSelectedBeads());
  };
  return (
    <div>
      X:
      <button onClick={onClickAlighXLeft}>
        <FontAwesomeIcon icon={faAlignLeft} color={uiButton} size={"lg"} />
      </button>
      <button onClick={onClickAlighXMiddle}>
        <FontAwesomeIcon icon={faAlignCenter} color={uiButton} size={"lg"} />
      </button>
      <button onClick={onClickAlighXRight}>
        <FontAwesomeIcon icon={faAlignRight} color={uiButton} size={"lg"} />
      </button>
      <br />
      <br />
      Y:
      <button onClick={onClickAlighYLeft}>
        <FontAwesomeIcon icon={faAlignLeft} color={uiButton} size={"lg"} />
      </button>
      <button onClick={onClickAlighYMiddle}>
        <FontAwesomeIcon icon={faAlignCenter} color={uiButton} size={"lg"} />
      </button>
      <button onClick={onClickAlighYRight}>
        <FontAwesomeIcon icon={faAlignRight} color={uiButton} size={"lg"} />
      </button>
      <br />
      <br />
      <button onClick={onClickDeleteSelected}>
        <FontAwesomeIcon icon={faTimes} color={uiButton} size={"lg"} />
      </button>
    </div>
  );
};

const BeadsRotate = () => {
  const dispatch = useDispatch();
  const selected = useSelector((state: Editor) => {
    const beadId = Object.keys(state.ui.selectedBeadsIds)[0];
    return state.appData.beads[beadId];
  });

  if (!selected) {
    return <React.Fragment />;
  }

  const onChange_beadsRotateX = (value?: number) => {
    let newValue = value;
    if (value === undefined || value === null || isNaN(value) === true) {
      newValue = 0;
    }
    dispatch(
      Actions.Toolbar.action_changeSelectedBeads_rotate(
        newValue,
        selected.rotateY,
        selected.rotateZ
      )
    );
  };
  const onChange_beadsRotateY = (value?: number) => {
    let newValue = value;
    if (value === undefined || value === null || isNaN(value) === true) {
      newValue = 90;
    }
    dispatch(
      Actions.Toolbar.action_changeSelectedBeads_rotate(
        selected.rotateX,
        newValue,
        selected.rotateZ
      )
    );
  };
  const onChange_beadsRotateZ = (value?: number) => {
    let newValue = value;
    if (value === undefined || value === null || isNaN(value) === true) {
      newValue = 0;
    }
    dispatch(
      Actions.Toolbar.action_changeSelectedBeads_rotate(
        selected.rotateX,
        selected.rotateY,
        newValue
      )
    );
  };

  return (
    <div>
      <FieldsetBeadRotate
        label={"RotateX"}
        value={selected.rotateX}
        onChange={onChange_beadsRotateX}
      />
      <FieldsetBeadRotate
        label={"RotateY"}
        value={selected.rotateY}
        onChange={onChange_beadsRotateY}
      />
      <FieldsetBeadRotate
        label={"RotateZ"}
        value={selected.rotateZ}
        onChange={onChange_beadsRotateZ}
      />
    </div>
  );
};

const BeadSize = () => {
  const dispatch = useDispatch();
  const selected = useSelector((state: Editor) => {
    const beadId = Object.keys(state.ui.selectedBeadsIds)[0];
    return state.appData.beads[beadId];
  });

  if (!selected) {
    return <React.Fragment />;
  }

  const onChange_beadWidth = (value) => {
    dispatch(Actions.Toolbar.action_changeSelectedBeads_width(value));
  };
  const onChange_beadHeight = (value) => {
    dispatch(Actions.Toolbar.action_changeSelectedBeads_height(value));
  };
  const onChange_beadDepth = (value) => {
    dispatch(Actions.Toolbar.action_changeSelectedBeads_depth(value));
  };
  const onChange_keepAspectRatio = (value) => {
    dispatch(Actions.Toolbar.action_changeSelectedBeads_keepAspectRatio(value));
  };
  const width = selected.width ? selected.width : 10;
  const height = selected.height ? selected.height : 10;
  const depth = selected.depth ? selected.depth : 10;
  const keepAspectRatio = selected.keepAspectRatio
    ? selected.keepAspectRatio
    : false;

  return (
    <div>
      <FieldsetWidthAndHeight
        height={height}
        width={width}
        keepAspectRatio={keepAspectRatio}
        onChange_beadHeight={onChange_beadHeight}
        onChange_beadWidth={onChange_beadWidth}
        onChange_keepAspectRatio={onChange_keepAspectRatio}
        depth={depth}
        onChange_beadDepth={onChange_beadDepth}
      />
    </div>
  );
};

const BeadType = () => {
  const dispatch = useDispatch();
  const selected = useSelector((state: Editor) => {
    const beadId = Object.keys(state.ui.selectedBeadsIds)[0];
    return state.appData.beads[beadId];
  });

  if (!selected) {
    return <React.Fragment />;
  }
  if (!selected.type) {
    return <React.Fragment />;
  }

  const onSelect = (type) => {
    dispatch(Actions.Toolbar.action_changeSelectedBeads_type(type));
  };

  return <FieldsetBeadType onChange={onSelect} value={selected.type} />;
};

const BeadColor = () => {
  const dispatch = useDispatch();
  const selected = useSelector((state: Editor) => {
    const beadId = Object.keys(state.ui.selectedBeadsIds)[0];
    return state.appData.beads[beadId];
  });

  if (!selected) {
    return <React.Fragment />;
  }

  if (!selected.color) {
    return <React.Fragment />;
  }

  const onSelect = (color) => {
    dispatch(Actions.Toolbar.action_changeSelectedBeads_color(color));
  };

  return <FieldsetColor onChange={onSelect} value={selected.color} />;
};

const BeadPreview = () => {
  const selected = useSelector((state: Editor) => {
    const beadId = Object.keys(state.ui.selectedBeadsIds)[0];
    return state.appData.beads[beadId];
  });

  if (!selected) {
    return <React.Fragment />;
  }
  return <FieldBeadPreview bead={selected} />;
};
