import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { Editor } from "../../../editorObjects/Editor";
import { FieldBeadPreview } from "../ui/FieldBeadPreview";
import { Actions } from "../../../immerReducers/store";
// import { FieldsetWidth } from "../ui/FieldsetWidth";
// import { FieldsetHeight } from "../ui/FieldsetHeight";
import { FieldsetColor } from "../ui/FieldsetColor";
import { FieldsetBeadType } from "../ui/FieldsetBeadType";
import { FieldsetWidthAndHeight } from "../ui/FieldsetWidthAndHeight";
import { FieldsetBeadRotate } from "../ui/FieldsetBeadRotate";
import { buildBead } from "../../../editorObjects/Bead";

export const ToolbarAddBeads = ({ isVisible }) => {
  if (isVisible === true) {
    return <Content />;
  }

  return <React.Fragment />;
};

const Content = () => {
  return (
    <React.Fragment>
      <BeadPreview />
      <BeadSize />
      <BeadColor />
      <BeadType />
      <BeadsRotate />
    </React.Fragment>
  );
};

const BeadsRotate = () => {
  const dispatch = useDispatch();

  const beadsTemplate = useSelector((state: Editor) => {
    return state.ui.beadsTemplate;
  });

  const onChange_beadsRotateX = (value?: number) => {
    let newValue: number = value ?? 90;
    if (value === undefined || value === null || isNaN(value) === true) {
      newValue = 0;
    }
    dispatch(Actions.Toolbar.action_updateBeadsTemplate("rotateX", newValue));
  };

  const onChange_beadsRotateY = (value?: number) => {
    let newValue: number = value ?? 0;
    if (value === undefined || value === null || isNaN(value) === true) {
      newValue = 0;
    }
    dispatch(Actions.Toolbar.action_updateBeadsTemplate("rotateY", newValue));
  };

  const onChange_beadsRotateZ = (value?: number) => {
    let newValue: number = value ?? 0;
    if (value === undefined || value === null || isNaN(value) === true) {
      newValue = 0;
    }
    dispatch(Actions.Toolbar.action_updateBeadsTemplate("rotateZ", newValue));
  };

  return (
    <div>
      <FieldsetBeadRotate
        label={"RotateX"}
        value={beadsTemplate.rotateX}
        onChange={onChange_beadsRotateX}
      />
      <FieldsetBeadRotate
        label={"RotateY"}
        value={beadsTemplate.rotateY}
        onChange={onChange_beadsRotateY}
      />
      <FieldsetBeadRotate
        label={"RotateZ"}
        value={beadsTemplate.rotateZ}
        onChange={onChange_beadsRotateZ}
      />
    </div>
  );
};

const BeadSize = () => {
  const dispatch = useDispatch();

  const beadsTemplate = useSelector((state: Editor) => {
    return state.ui.beadsTemplate;
  });

  const onChange_beadWidth = (value) => {
    dispatch(Actions.Toolbar.action_updateBeadsTemplate("width", value));
  };

  const onChange_beadHeight = (value) => {
    dispatch(Actions.Toolbar.action_updateBeadsTemplate("height", value));
  };

  const onChange_beadDepth = (value) => {
    dispatch(Actions.Toolbar.action_updateBeadsTemplate("depth", value));
  };

  const onChange_keepAspectRatio = (value) => {
    dispatch(
      Actions.Toolbar.action_updateBeadsTemplate("keepAspectRatio", value)
    );
  };

  const width = beadsTemplate.width ? beadsTemplate.width : 10;
  const height = beadsTemplate.height ? beadsTemplate.height : 10;
  const depth = beadsTemplate.depth ? beadsTemplate.depth : 10;
  const keepAspectRatio = beadsTemplate.keepAspectRatio
    ? beadsTemplate.keepAspectRatio
    : false;

  return (
    <div>
      <FieldsetWidthAndHeight
        height={height}
        width={width}
        keepAspectRatio={keepAspectRatio}
        onChange_beadHeight={onChange_beadHeight}
        onChange_beadWidth={onChange_beadWidth}
        onChange_keepAspectRatio={onChange_keepAspectRatio}
        depth={depth}
        onChange_beadDepth={onChange_beadDepth}
      />
    </div>
  );
};

const BeadType = () => {
  const dispatch = useDispatch();

  const beadsTemplate = useSelector((state: Editor) => {
    return state.ui.beadsTemplate;
  });

  const onSelect = (type) => {
    dispatch(Actions.Toolbar.action_updateBeadsTemplate("type", type));
  };

  return <FieldsetBeadType onChange={onSelect} value={beadsTemplate.type} />;
};

const BeadColor = () => {
  const dispatch = useDispatch();

  const beadsTemplate = useSelector((state: Editor) => {
    return state.ui.beadsTemplate;
  });

  const onSelect = (color) => {
    dispatch(Actions.Toolbar.action_updateBeadsTemplate("color", color));
  };

  return <FieldsetColor onChange={onSelect} value={beadsTemplate.color} />;
};

const BeadPreview = () => {
  const beadsTemplate = useSelector((state: Editor) => {
    return state.ui.beadsTemplate;
  });

  const bead = buildBead(beadsTemplate);

  return <FieldBeadPreview bead={bead} />;
};
