// import { log, warn, error } from "three";
const { log, warn, error } = console;

export type EventCallback = (data: any, state: any) => void;
export interface Events {
  [key: string]: EventCallback[];
}
export class EventEmitter {
  _events: Events;
  constructor() {
    this._events = {};
  }

  on(name: string, listener: EventCallback) {
    if (!this._events[name]) {
      this._events[name] = [];
    }

    this._events[name].push(listener);
  }

  removeListener(name: string, listenerToRemove: EventCallback) {
    if (!this._events[name]) {
      error(`Can't remove a listener. Event "${name}" doesn't exits.`);
      return;
    }

    const filterListeners = (listener) => listener !== listenerToRemove;

    this._events[name] = this._events[name].filter(filterListeners);
  }

  emit(name: string, data: any, state: any) {
    log("EventEmitter:emit:", name, data);

    if (!this._events[name]) {
      warn(`Can't emit an event. Event "${name}" doesn't exits.`);
      return;
    }

    const fireCallbacks = (callback: EventCallback) => {
      callback(data, state);
    };

    this._events[name].forEach(fireCallbacks);
  }
}

let internalEventEmiter: EventEmitter | undefined;

export const getEventEmitter = () => {
  if (internalEventEmiter === undefined) {
    internalEventEmiter = new EventEmitter();
  }
  return internalEventEmiter;
};

