import { appStore } from "./../immerReducers/appStore";
import { Actions } from "../immerReducers/store";
import { OBJLoader } from "three/examples/jsm/loaders/OBJLoader";
import * as THREE from "three";
import { EditorMode, Point } from "../editorObjects/Editor";
import { Frustum, Vector2, Vector3 } from "three";
import _ from "lodash";
/**@ts-ignore */
// eslint-disable-next-line import/no-webpack-loader-syntax
import beadModelText from "!!raw-loader!./models3d/bead_2020_02_05.obj";
/**@ts-ignore */
// eslint-disable-next-line import/no-webpack-loader-syntax
import diamondModelText from "!!raw-loader!./models3d/diamond.obj";
/**@ts-ignore */
// eslint-disable-next-line import/no-webpack-loader-syntax
import modelToho15v3a from "!!raw-loader!./models3d/bead_toho_15_v3a.obj";
import { initEventEmitterHandlers } from "./eventEmitterHandlers";
import {
  getBeadIdFromThreeName,
  getThreeNameFromBeadId,
} from "../editorObjects/Bead";

export type Base3dModelNames = "baseBead" | "diamond" | "modelToho15v3a";
export type Base3dModels = { [day in Base3dModelNames]: THREE.Object3D | null };
export const renderThreejs = (el: HTMLElement | null) => {
  initCanvasApp(el);
};

const initCanvasApp = (el: HTMLElement | null) => {
  let camera: THREE.OrthographicCamera,
    scene: THREE.Scene,
    renderer: THREE.WebGLRenderer,
    base3dModels: Base3dModels = {
      baseBead: null,
      diamond: null,
      modelToho15v3a: null,
    },
    mouse_intercestion = new THREE.Vector2(1, 1),
    mouseDragHelper = new MouseDragHelper(),
    selectionBox = new SelectionFilter(),
    mouseSelectionHelper = new MouseSelectionHelper();

  init();

  var sceneScale = 1;

  function init() {
    if (el !== null) {
      const { width, height } = el.getBoundingClientRect();
      camera = new THREE.OrthographicCamera(
        width / -2,
        width / 2,
        height / 2,
        height / -2,
        0,
        1000
      );
    }
    camera.position.set(0, 0, 500);
    camera.lookAt(0, 0, 0);

    scene = new THREE.Scene();
    scene.background = new THREE.Color("#F1F3F3");
    // scene.background = new THREE.TextureLoader().load(require('./img/background.jpg').default);

    {
      var bg_geometry = new THREE.PlaneGeometry(1000, 1000, 1);
      var bg_material = new THREE.MeshBasicMaterial({
        color: "#ccc",
        side: THREE.DoubleSide,
      });

      var bg_plane = new THREE.Mesh(bg_geometry, bg_material);
      // scene.add(bg_plane);
    }

    {
      var bg_geometry = new THREE.PlaneGeometry(1000, 1000, 1);
      var bg_material = new THREE.MeshBasicMaterial({
        color: "#ccc",
        side: THREE.DoubleSide,
      });

      var bg_plane = new THREE.Mesh(bg_geometry, bg_material);
      // scene.add(bg_plane);
    }

    //@TODO: Przelicz wielkosci aby uzyc ich przy dodawaniu kolek
    {
      try {
        const objLoader = new OBJLoader();
        const object = objLoader.parse(beadModelText);
        console.log("OBJLoader: ", "odczytano plik beadModelText ");
        object.traverse((child) => {
          if (child instanceof THREE.Mesh) {
            child.position.set(0, 0, 0);
            child.scale.x = 1;
            child.scale.y = 1;
            child.scale.z = 1;
          }
        });
        base3dModels["baseBead"] = object.clone();
      } catch (err) {
        console.error("OBJLoader: ", "nie mozna doczytac beadModelText");
      }
    }

    {
      try {
        const objLoader = new OBJLoader();
        const object = objLoader.parse(diamondModelText);
        console.log("OBJLoader: ", "odczytano plik diamondModelText ");
        object.traverse((child) => {
          if (child instanceof THREE.Mesh) {
            child.position.set(0, 0, 0);
            child.scale.x = 1;
            child.scale.y = 1;
            child.scale.z = 1;
          }
        });
        base3dModels["diamond"] = object.clone();
      } catch (err) {
        console.error("OBJLoader: ", "nie mozna doczytac diamondModelText");
      }
    }

    {
      try {
        const objLoader = new OBJLoader();
        const object = objLoader.parse(modelToho15v3a);
        console.log("OBJLoader: ", "odczytano plik modelToho15v3a ");
        object.traverse((child) => {
          if (child instanceof THREE.Mesh) {
            child.position.set(0, 0, 0);
            child.scale.x = 1;
            child.scale.y = 1;
            child.scale.z = 1;
          }
        });
        base3dModels["modelToho15v3a"] = object.clone();
      } catch (err) {
        console.error("OBJLoader: ", "nie mozna doczytac modelToho15v3a");
      }
    }

    // @INFO: Loading external models
    // {
    //   const objLoader = new OBJLoader();
    //   const baseBeadPath = "./3dmodels/bead_2020_02_05.obj";
    //   objLoader.load(
    //     baseBeadPath,
    //     (object) => {
    //       console.log("OBJLoader: ", "odczytano plik: ", baseBeadPath);
    //       object.position.set(10, 10, 10);
    //       object.scale.x = 10;
    //       object.scale.y = 10;
    //       object.scale.z = 10;
    //       base3dModels["baseBead"] = object.clone();
    //     },
    //     (xhr) => {
    //       console.log(
    //         "OBJLoader: ",
    //         (xhr.loaded / xhr.total) * 100 + "% loaded"
    //       );
    //     },
    //     (error) => {
    //       console.error("OBJLoader: ", error);
    //     }
    //   );
    // }

    renderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.setPixelRatio(window.devicePixelRatio);

    if (el !== null) {
      const { width, height } = el.getBoundingClientRect();
      renderer.setSize(width, height);
      el.appendChild(renderer.domElement);
    }

    window.addEventListener("resize", onWindowResize, false);

    if (el !== null) {
      el.addEventListener("pointerdown", onPointerDown, false);
      el.addEventListener("pointermove", onPointerMove, false);
      el.addEventListener("pointerup", onPointerUp, false);
      el.addEventListener("wheel", onWheel, false);
    }
    mouseSelectionHelper.setRenderer(renderer);

    initEventEmitterHandlers(scene, base3dModels);
    selectionBox.setCamera(camera);
    selectionBox.setScene(scene);
  }

  function onWheel(event) {
    event.preventDefault();
    if (el !== null) {
      const { width, height, top, left } = el.getBoundingClientRect();
      // const __x1 = event.clientX - left - (width * sceneScale) / 2;
      // const __y1 = (height * sceneScale) / 2 - (event.clientY - top);

      const screenX = event.clientX - left;
      const screenY = event.clientY - top;

      const cameraX = camera.position.x;
      const cameraY = camera.position.y;

      const mouseXInWorldBefore = (screenX - width / 2) * sceneScale;
      const mouseYInWorldBefore = -1 * (screenY - height / 2) * sceneScale;

      const zoomChange = 0.02 * 5;
      const zoomMax = 100;
      const zoomMin = 0.0125;
      sceneScale += Math.sign(event.deltaY) * zoomChange;
      sceneScale = Math.min(Math.max(zoomMin, sceneScale), zoomMax);

      const mouseXInWorldAfter = (screenX - width / 2) * sceneScale;
      const mouseYInWorldAfter = -1 * (screenY - height / 2) * sceneScale;

      camera.left = (width * sceneScale) / -2;
      camera.right = (width * sceneScale) / 2;
      camera.top = (height * sceneScale) / 2;
      camera.bottom = (height * sceneScale) / -2;
      camera.updateProjectionMatrix();

      const diffX = mouseXInWorldBefore - mouseXInWorldAfter;
      const diffY = mouseYInWorldBefore - mouseYInWorldAfter;

      camera.position.x = cameraX + diffX;
      camera.position.y = cameraY + diffY;
      camera.lookAt(camera.position.x, camera.position.y, 0);
    }
  }

  const screenToWorld = (clientX, clientY): THREE.Vector2 => {
    if (el === null) {
      throw new Error("no el in dom");
    }

    const { width, height, top, left } = el.getBoundingClientRect();
    const screenX = clientX - left;
    const screenY = clientY - top;
    const x = (screenX - width / 2) * sceneScale + camera.position.x;
    const y = (-screenY + height / 2) * sceneScale + camera.position.y;
    return new THREE.Vector2(x, y);
  };

  let isCameraGrabed = false;
  let startDragMousePosition = new THREE.Vector2();
  let startDragCamera = new THREE.Vector2();
  let boundingClientRect: DOMRect | undefined;

  var raycaster = new THREE.Raycaster();
  var mouse = new THREE.Vector2();

  function onPointerDown(event: PointerEvent) {
    const { getState } = appStore;
    const state = getState();

    if (state.mode === EditorMode.Idle) {
      isCameraGrabed = true;
      if (el !== null) {
        const { top, left } = el.getBoundingClientRect();
        boundingClientRect = el.getBoundingClientRect();

        const screenX = event.clientX - left;
        const screenY = event.clientY - top;
        startDragMousePosition.x = screenX;
        startDragMousePosition.y = screenY;

        startDragCamera.x = camera.position.x;
        startDragCamera.y = camera.position.y;
      }
    } else if (state.mode === EditorMode.EditBeads) {
      if (el !== null) {
        boundingClientRect = el.getBoundingClientRect();
      }
      if (boundingClientRect === undefined) {
        return;
      }

      const { x, y } = screenToWorld(event.clientX, event.clientY);
      mouseDragHelper.onDown(x, y);

      if (state.ui.selectedBeadsIdsCount === 0) {
        const { width, height, top, left } = boundingClientRect;
        const screenX = event.clientX - left;
        const screenY = event.clientY - top;
        mouseSelectionHelper.onDown(screenX, screenY);
        selectionBox.startPoint.set(
          ((event.clientX - left) / width) * 2 - 1,
          -((event.clientY - top) / height) * 2 + 1,
          0
        );
      } else {
        const { width, height, top, left } = boundingClientRect;
        mouse.x = ((event.clientX - left) / width) * 2 - 1;
        mouse.y = -((event.clientY - top) / height) * 2 + 1;
        raycaster.setFromCamera(mouse, camera);
        const intersects = raycaster.intersectObjects(scene.children, true);
        const beadsIds: string[] = [];
        intersects.forEach((e) => {
          const id = getBeadIdFromThreeName(e.object.name);
          if (id !== undefined) {
            if (state.appData.beads[id]) {
              beadsIds.push(id);
            } else {
              console.warn("bead id missing in store", id);
            }
          }
        });

        if (
          beadsIds.length > 0 &&
          _.difference(beadsIds, Object.keys(state.ui.selectedBeadsIds))
            .length === 0
        ) {
          mouseDragHelper.shouldBehaveLikeMove = true;
        } else {
          const { width, height, top, left } = boundingClientRect;
          const screenX = event.clientX - left;
          const screenY = event.clientY - top;
          mouseSelectionHelper.onDown(screenX, screenY);
          selectionBox.startPoint.set(
            ((event.clientX - left) / width) * 2 - 1,
            -((event.clientY - top) / height) * 2 + 1,
            0
          );
        }
      }
    } else if (state.mode === EditorMode.DeleteBeads) {
      if (el !== null) {
        boundingClientRect = el.getBoundingClientRect();
      }
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      mouseDragHelper.onDown(x, y);
      if (el === null) {
        throw new Error("no el in dom");
      }
      const { width, height, top, left } = el.getBoundingClientRect();
      const screenX = event.clientX - left;
      const screenY = event.clientY - top;
      mouseSelectionHelper.onDown(screenX, screenY);
      // selectionBox.startPoint.set(screenX, screenY, 0.5);
    } else if (state.mode === EditorMode.PaintBeads) {
      if (el !== null) {
        boundingClientRect = el.getBoundingClientRect();
      }
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      mouseDragHelper.onDown(x, y);
    } else if (state.mode === EditorMode.AddRegion) {
      if (el !== null) {
        boundingClientRect = el.getBoundingClientRect();
      }
      if (boundingClientRect === undefined) {
        return;
      }
      const { top, left } = boundingClientRect;
      const screenX = event.clientX - left;
      const screenY = event.clientY - top;
      mouseSelectionHelper.onDown(screenX, screenY);
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      mouseDragHelper.onDown(x, y);
    }
  }

  function onPointerUp(event) {
    const { dispatch, getState } = appStore;
    const state = getState();
    if (state.mode === EditorMode.AddBeads) {
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      dispatch(
        Actions.Draggable.action_addBeadOnClick({
          x,
          y,
        })
      );
    } else if (state.mode === EditorMode.Idle) {
      if (isCameraGrabed === true) {
        isCameraGrabed = false;
      }
    } else if (state.mode === EditorMode.EditBeads) {
      if (mouseDragHelper.shouldBehaveLikeMove === true) {
        // if (mouseSelectionBeads.isDragged === true) {
        const diffX = mouseDragHelper.end.x - mouseDragHelper.start.x;
        const diffY = mouseDragHelper.end.y - mouseDragHelper.start.y;
        mouseDragHelper.shouldBehaveLikeMove = false;
        dispatch(
          Actions.MouseSelection.action_movedSelectedBeads(diffX, diffY)
        );
        // } else if (
        //   mouseSelectionBeads.isDragged === false &&
        //   mouseSelectionBeads.isDragStarted === true
        // ) {
        //   if (boundingClientRect !== undefined) {
        //     const { width, height, top, left } = boundingClientRect;
        //     mouse.x = ((event.clientX - left) / width) * 2 - 1;
        //     mouse.y = -((event.clientY - top) / height) * 2 + 1;
        //     raycaster.setFromCamera(mouse, camera);
        //     const intersects = raycaster.intersectObjects(scene.children, true);
        //     const beadsIds: string[] = [];
        //     intersects.forEach((e) => {
        //       const name = e.object.name;
        //       if (name && name.length > 0) {
        //         const id = name.includes(CHILD_OF)
        //           ? name.replace(CHILD_OF, "")
        //           : name;
        //         if (state.appData.beads[id]) {
        //           beadsIds.push(id);
        //         }
        //       }
        //     });
        //     dispatch(Actions.MouseSelection.action_selectBeads(beadsIds));
        //   }
        // }
      } else {
        if (mouseDragHelper.isDragged === true) {
          // do nothing
        } else if (
          mouseDragHelper.isDragged === false &&
          mouseDragHelper.isDragStarted === true
        ) {
          if (boundingClientRect === undefined) {
            return;
          }

          const { width, height, top, left } = boundingClientRect;
          mouse.x = ((event.clientX - left) / width) * 2 - 1;
          mouse.y = -((event.clientY - top) / height) * 2 + 1;
          raycaster.setFromCamera(mouse, camera);
          const intersects = raycaster.intersectObjects(scene.children, true);
          const beadsIds: string[] = [];
          intersects.forEach((e) => {
            const id = getBeadIdFromThreeName(e.object.name);
            if (id !== undefined) {
              if (state.appData.beads[id]) {
                beadsIds.push(id);
              } else {
                console.warn("bead id missing in store", id);
              }
            }
          });

          dispatch(Actions.MouseSelection.action_selectBeads(beadsIds));

          // @TODO: temporary removed
          // const { x, y } = screenToWorld(event.clientX, event.clientY);
          // const beadsIds: string[] = [];
          // Object.keys(state.appData.beads).forEach((beadId) => {
          //   const bead = state.appData.beads[beadId];
          //   const leftTop = buildPoint(
          //     bead.position.x - bead.width / 2,
          //     bead.position.y - bead.height / 2
          //   );
          //   const rightBottom = buildPoint(
          //     bead.position.x + bead.width / 2,
          //     bead.position.y + bead.height / 2
          //   );
          //   if (contain(leftTop, rightBottom, buildPoint(x, y))) {
          //     beadsIds.push(beadId);
          //   }
          // });
          // dispatch(Actions.MouseSelection.action_selectBeads(beadsIds));
        } else {
          dispatch(Actions.MouseSelection.action_selectBeads([]));
        }
      }
      mouseDragHelper.onUp();
      mouseSelectionHelper.onUp();
    } else if (state.mode === EditorMode.DeleteBeads) {
      if (mouseDragHelper.isDragged === true) {
        // do nothing
      } else if (
        mouseDragHelper.isDragged === false &&
        mouseDragHelper.isDragStarted === true
      ) {
        if (el !== null) {
          const { width, height, top, left } = el.getBoundingClientRect();
          mouse.x = ((event.clientX - left) / width) * 2 - 1;
          mouse.y = -((event.clientY - top) / height) * 2 + 1;

          raycaster.setFromCamera(mouse, camera);
          const intersects = raycaster.intersectObjects(scene.children, true);
          const beadsIds: string[] = [];
          intersects.forEach((e) => {
            const id = getBeadIdFromThreeName(e.object.name);
            if (id !== undefined) {
              if (state.appData.beads[id]) {
                beadsIds.push(id);
              } else {
                console.warn("bead id missing in store", id);
              }
            }
          });
          dispatch(Actions.Toolbar.action_deleteBeads(beadsIds));
        }
      } else {
        // do nothing
      }
      mouseDragHelper.onUp();
      mouseSelectionHelper.onUp();
    } else if (state.mode === EditorMode.PaintBeads) {
      if (mouseDragHelper.isDragged === true) {
        // do nothing
      } else if (
        mouseDragHelper.isDragged === false &&
        mouseDragHelper.isDragStarted === true
      ) {
        if (boundingClientRect === undefined) {
          return;
        }
        const { width, height, top, left } = boundingClientRect;

        mouse.x = ((event.clientX - left) / width) * 2 - 1;
        mouse.y = -((event.clientY - top) / height) * 2 + 1;

        raycaster.setFromCamera(mouse, camera);
        const intersects = raycaster.intersectObjects(scene.children, true);

        if (intersects.length > 0) {
          const beadsIds: string[] = [];
          intersects.forEach((e) => {
            const id = getBeadIdFromThreeName(e.object.name);
            if (id !== undefined) {
              if (state.appData.beads[id]) {
                beadsIds.push(id);
              } else {
                console.warn("bead id missing in store", id);
              }
            }
          });
          if (beadsIds.length > 0) {
            dispatch(
              Actions.Toolbar.action_changeBeadColorToBeadTemplate(beadsIds)
            );
          }
        }
      }

      mouseDragHelper.onUp();
      mouseSelectionHelper.onUp();
    } else if (state.mode === EditorMode.AddRegion) {
      dispatch(
        Actions.Region.action_addRegion(
          mouseDragHelper.start as Point,
          mouseDragHelper.end as Point
        )
      );
      mouseDragHelper.onUp();
      mouseSelectionHelper.onUp();
    }
  }

  function onWindowResize() {
    // console.log("onWindowResize");
    // const { dispatch, subscribe, getState } = appStore;
    // const state = getState();

    if (el !== null) {
      const { width, height } = el.getBoundingClientRect();

      // camera.aspect = width / height;
      // (camera as THREE.OrthographicCamera)
      camera.left = width / -2;
      camera.right = width / 2;
      camera.top = height / 2;
      camera.bottom = height / -2;
      camera.updateProjectionMatrix();
      renderer.setSize(width, height);
    }
  }

  function onPointerMove(event) {
    if (boundingClientRect) {
      const { dispatch, getState } = appStore;
      const state = getState();
      if (state.mode === EditorMode.Idle) {
        const { width, height, top, left } = boundingClientRect;

        // Used in: Clicked at item in world?
        mouse_intercestion.x = ((event.clientX - left) / width) * 2 - 1;
        mouse_intercestion.y = -((event.clientY - top) / height) * 2 + 1;

        const screenX = event.clientX - left;
        const screenY = event.clientY - top;

        if (isCameraGrabed === true) {
          const screenMouseDiffX = startDragMousePosition.x - screenX;
          const screenMouseDiffY = startDragMousePosition.y - screenY;

          const mouseDiffXInWorldSpace = screenMouseDiffX * sceneScale;
          const mouseDiffYInWorldSpace = screenMouseDiffY * sceneScale;

          const cameraX = startDragCamera.x + mouseDiffXInWorldSpace;
          const cameraY = startDragCamera.y - mouseDiffYInWorldSpace;
          camera.position.x = cameraX;
          camera.position.y = cameraY;
          camera.lookAt(cameraX, cameraY, 0);
        }
      } else if (state.mode === EditorMode.EditBeads) {
        const { width, height, top, left } = boundingClientRect;

        if (mouseDragHelper.shouldBehaveLikeMove === false) {
          if (mouseDragHelper.isDragStarted === true) {
            const { x, y } = screenToWorld(event.clientX, event.clientY);

            mouseDragHelper.onMove(x, y);

            if (mouseDragHelper.isDragStarted === true) {
              selectionBox.endPoint.set(
                ((event.clientX - left) / width) * 2 - 1,
                -((event.clientY - top) / height) * 2 + 1,
                1
              );

              const allSelected = selectionBox.select();
              const beadsIds: string[] = [];
              allSelected.forEach((object) => {
                const id = getBeadIdFromThreeName(object.name);
                if (id !== undefined) {
                  if (state.appData.beads[id]) {
                    beadsIds.push(id);
                  } else {
                    console.warn("bead id missing in store", id);
                  }
                }
              });
              dispatch(Actions.MouseSelection.action_selectBeads(beadsIds));
            }

            // @INFO: Moved to raycasting
            // if (mouseSelectionBeads.isDragged === true) {
            //   const beadsIds: string[] = [];
            //   Object.keys(state.appData.beads).forEach((beadId) => {
            //     const bead = state.appData.beads[beadId];
            //     if (mouseSelectionBeads.contain(bead.position)) {
            //       beadsIds.push(beadId);
            //     }
            //   });
            //   dispatch(Actions.MouseSelection.action_selectBeads(beadsIds));
            // }
          }

          const screenX = event.clientX - left;
          const screenY = event.clientY - top;

          mouseSelectionHelper.onMove(screenX, screenY);
        } else {
          const { x, y } = screenToWorld(event.clientX, event.clientY);
          mouseDragHelper.onMove(x, y);
          const diffX = mouseDragHelper.end.x - mouseDragHelper.start.x;
          const diffY = mouseDragHelper.end.y - mouseDragHelper.start.y;
          Object.keys(state.ui.selectedBeadsIds).forEach((beadId) => {
            const name = getThreeNameFromBeadId(beadId);
            const object = scene.getObjectByName(name);
            if (object === undefined) {
              return;
            }

            object.position.x = state.appData.beads[beadId].position.x + diffX;
            object.position.y = state.appData.beads[beadId].position.y + diffY;
          });
        }
      } else if (state.mode === EditorMode.PaintBeads) {
        if (mouseDragHelper.isDragStarted === true) {
          const { width, height, top, left } = boundingClientRect;

          mouse.x = ((event.clientX - left) / width) * 2 - 1;
          mouse.y = -((event.clientY - top) / height) * 2 + 1;

          raycaster.setFromCamera(mouse, camera);
          const intersects = raycaster.intersectObjects(scene.children, true);

          if (intersects.length > 0) {
            const beadsIds: string[] = [];
            intersects.forEach((e) => {
              const id = getBeadIdFromThreeName(e.object.name);
              if (id !== undefined) {
                if (state.appData.beads[id]) {
                  beadsIds.push(id);
                } else {
                  console.warn("bead id missing in store", id);
                }
              }
            });
            if (beadsIds.length > 0) {
              dispatch(
                Actions.Toolbar.action_changeBeadColorToBeadTemplate(beadsIds)
              );
            }
          }

          const screenX = event.clientX - left;
          const screenY = event.clientY - top;
          mouseSelectionHelper.onMove(screenX, screenY);
        }
      } else if (state.mode === EditorMode.AddRegion) {
        const { width, height, top, left } = boundingClientRect;
        const screenX = event.clientX - left;
        const screenY = event.clientY - top;
        mouseSelectionHelper.onMove(screenX, screenY);
        const { x, y } = screenToWorld(event.clientX, event.clientY);
        mouseDragHelper.onMove(x, y);
      }
    }
  }

  function resizeRendererToDisplaySize(renderer) {
    const canvas = renderer.domElement;
    const width = canvas.clientWidth;
    const height = canvas.clientHeight;
    const needResize = canvas.width !== width || canvas.height !== height;
    if (needResize) {
      renderer.setSize(width, height, false);
    }
    return needResize;
  }

  function render() {
    if (resizeRendererToDisplaySize(renderer)) {
      // @INFO: only for perspective camera
      // const canvas = renderer.domElement;
      // camera.aspect = canvas.clientWidth / canvas.clientHeight;
      camera.updateProjectionMatrix();
    }

    renderer.render(scene, camera);
    requestAnimationFrame(render);
  }

  requestAnimationFrame(render);
};

class MouseDragHelper {
  isDragged: boolean = false;
  isDragStarted: boolean = false;
  end: { x: number; y: number } = { x: 0, y: 0 };
  start: { x: number; y: number } = { x: 0, y: 0 };
  shouldBehaveLikeMove: boolean = false;

  onUp() {
    this.isDragStarted = false;
    this.isDragged = false;
    this.start.x = 0;
    this.start.y = 0;
    this.end.x = 0;
    this.end.y = 0;
    this.shouldBehaveLikeMove = false;
  }

  onDown(x: number, y: number) {
    this.isDragStarted = true;
    this.start.x = x;
    this.start.y = y;
    this.end.x = x;
    this.end.y = y;
  }

  onMove(x: number, y: number) {
    if (this.isDragStarted == true) {
      this.end.x = x;
      this.end.y = y;
      if (this.isDragged === false) {
        const { start, end } = this;
        const deltaX = end.x - start.x;
        const deltaY = end.y - start.y;

        if (Math.abs(deltaX) > 5 || Math.abs(deltaY) > 5) {
          this.isDragged = true;
        }
      }
    }
  }

  // @INFO: Moved to point raycasting / frustum raycasting
  // contain(p: { x: number; y: number }) {
  //   const { start, end } = this;
  //
  //   const p1_x = Math.min(start.x, end.x);
  //   const p1_y = Math.min(start.y, end.y);
  //
  //   const p2_x = Math.max(start.x, end.x);
  //   const p2_y = Math.max(start.y, end.y);
  //
  //   return p.x > p1_x && p.x < p2_x && p.y > p1_y && p.y < p2_y;
  // }
}

class MouseSelectionHelper {
  element: HTMLDivElement;
  renderer: any;
  startPoint: Vector2;
  pointTopLeft: Vector2;
  pointBottomRight: Vector2;
  isDown: boolean;

  constructor() {
    this.element = document.createElement("div");
    this.element.style.pointerEvents = "none";
    this.element.style.background = "rgba(75, 160, 255, 0.3)";
    this.element.style.border = "1px solid #55aaff";
    this.element.style.position = "absolute";
    this.element.style.zIndex = "2";
    this.startPoint = new Vector2();
    this.pointTopLeft = new Vector2();
    this.pointBottomRight = new Vector2();
    this.isDown = false;
  }

  setRenderer(renderer) {
    this.renderer = renderer;
  }

  onDown(screenX: number, screenY: number) {
    this.isDown = true;
    this.renderer.domElement.parentElement.appendChild(this.element);

    // this.element.setAttribute("style", `
    //   left: ${screenX}px;
    //   top: ${screenY}px;
    //   width: 0px;
    //   height: 0px;
    // `);
    this.element.style.left = screenX + "px";
    this.element.style.top = screenY + "px";
    this.element.style.width = "0px";
    this.element.style.height = "0px";

    this.startPoint.x = screenX;
    this.startPoint.y = screenY;
  }

  onMove(screenX: number, screenY: number) {
    if (this.isDown) {
      this.pointBottomRight.x = Math.max(this.startPoint.x, screenX);
      this.pointBottomRight.y = Math.max(this.startPoint.y, screenY);
      this.pointTopLeft.x = Math.min(this.startPoint.x, screenX);
      this.pointTopLeft.y = Math.min(this.startPoint.y, screenY);

      this.element.style.left = this.pointTopLeft.x + "px";
      this.element.style.top = this.pointTopLeft.y + "px";
      this.element.style.width =
        this.pointBottomRight.x - this.pointTopLeft.x + "px";
      this.element.style.height =
        this.pointBottomRight.y - this.pointTopLeft.y + "px";
    }
  }

  onUp() {
    if (this.element.parentElement !== null) {
      this.element.parentElement.removeChild(this.element);
    }
  }
}

class SelectionFilter {
  frustum: THREE.Frustum = new Frustum();
  center: THREE.Vector3 = new Vector3();

  tmpPoint: THREE.Vector3 = new Vector3();

  vecNear: THREE.Vector3 = new Vector3();
  vecTopLeft: THREE.Vector3 = new Vector3();
  vecTopRight: THREE.Vector3 = new Vector3();
  vecDownRight: THREE.Vector3 = new Vector3();
  vecDownLeft: THREE.Vector3 = new Vector3();
  vecFarTopLeft: THREE.Vector3 = new Vector3();
  vecFarTopRight: THREE.Vector3 = new Vector3();
  vecFarDownRight: THREE.Vector3 = new Vector3();
  vecFarDownLeft: THREE.Vector3 = new Vector3();

  vectemp1: THREE.Vector3 = new Vector3();
  vectemp2: THREE.Vector3 = new Vector3();
  vectemp3: THREE.Vector3 = new Vector3();
  startPoint: THREE.Vector3;
  endPoint: THREE.Vector3;
  collection: any[];
  deep: number = Number.MAX_VALUE;
  camera?: THREE.OrthographicCamera;
  scene?: THREE.Scene;

  constructor() {
    this.startPoint = new Vector3();
    this.endPoint = new Vector3();
    this.collection = [];
  }

  setCamera(camera: THREE.OrthographicCamera) {
    this.camera = camera;
  }

  setScene(scene: THREE.Scene) {
    this.scene = scene;
  }

  select(startPoint?: THREE.Vector3, endPoint?: THREE.Vector3) {
    this.startPoint = startPoint ?? this.startPoint;
    this.endPoint = endPoint ?? this.endPoint;
    this.collection = [];

    if (!this.scene) {
      return this.collection;
    }

    this.updateFrustum(this.startPoint, this.endPoint);

    // @TODO: NEED pixel-perfect match
    this.searchChildInFrustum(this.frustum, this.scene);
    // this.scene.children.forEach((child) => {
    //   child.traverse((object) => {
    //     if (object instanceof THREE.Mesh && object.name.length > 0) {
    //       // @TODO: NEED pixel-perfect match
    //       const bbox = new THREE.Box3().setFromObject(object);
    //       if (this.frustum.intersectsBox(bbox)) {
    //         this.collection.push(object);
    //       }
    //       if (this.frustum.intersectsObject(object)) {
    //         this.collection.push(object);
    //       }
    //     }
    //   });
    // });

    return this.collection;
  }

  updateFrustum(_startPoint?: THREE.Vector3, _endPoint?: THREE.Vector3) {
    if (!this.camera) {
      return;
    }
    const startPoint = _startPoint ?? this.startPoint;
    const endPoint = _endPoint ?? this.endPoint;

    // Avoid invalid frustum
    if (startPoint.x === endPoint.x) {
      endPoint.x += Number.EPSILON;
    }

    if (startPoint.y === endPoint.y) {
      endPoint.y += Number.EPSILON;
    }

    this.camera.updateProjectionMatrix();
    this.camera.updateMatrixWorld();

    if (this.camera.isOrthographicCamera) {
      var left = Math.min(startPoint.x, endPoint.x);
      var top = Math.max(startPoint.y, endPoint.y);
      var right = Math.max(startPoint.x, endPoint.x);
      var down = Math.min(startPoint.y, endPoint.y);

      this.vecTopLeft.set(left, top, -1);
      this.vecTopRight.set(right, top, -1);
      this.vecDownRight.set(right, down, -1);
      this.vecDownLeft.set(left, down, -1);

      this.vecFarTopLeft.set(left, top, 1);
      this.vecFarTopRight.set(right, top, 1);
      this.vecFarDownRight.set(right, down, 1);
      this.vecFarDownLeft.set(left, down, 1);

      this.vecTopLeft.unproject(this.camera);
      this.vecTopRight.unproject(this.camera);
      this.vecDownRight.unproject(this.camera);
      this.vecDownLeft.unproject(this.camera);

      this.vecFarTopLeft.unproject(this.camera);
      this.vecFarTopRight.unproject(this.camera);
      this.vecFarDownRight.unproject(this.camera);
      this.vecFarDownLeft.unproject(this.camera);

      var planes = this.frustum.planes;

      planes[0].setFromCoplanarPoints(
        this.vecTopLeft,
        this.vecFarTopLeft,
        this.vecFarTopRight
      );
      planes[1].setFromCoplanarPoints(
        this.vecTopRight,
        this.vecFarTopRight,
        this.vecFarDownRight
      );
      planes[2].setFromCoplanarPoints(
        this.vecFarDownRight,
        this.vecFarDownLeft,
        this.vecDownLeft
      );
      planes[3].setFromCoplanarPoints(
        this.vecFarDownLeft,
        this.vecFarTopLeft,
        this.vecTopLeft
      );
      planes[4].setFromCoplanarPoints(
        this.vecTopRight,
        this.vecDownRight,
        this.vecDownLeft
      );
      planes[5].setFromCoplanarPoints(
        this.vecFarDownRight,
        this.vecFarTopRight,
        this.vecFarTopLeft
      );
      planes[5].normal.multiplyScalar(-1);
    } else {
      console.error("THREE.SelectionBox: Unsupported camera type.");
    }
  }

  // @INFO: Not used, there is need for pixel perfect raycasting
  searchChildInFrustum(frustum: THREE.Frustum, object: any) {
    if (object.isMesh || object.isLine || object.isPoints) {
      if (object.material !== undefined) {
        if (object.geometry.boundingSphere === null) {
          object.geometry.computeBoundingSphere();
        }

        const bbox = new THREE.Box3().setFromObject(object);

        if (frustum.intersectsBox(bbox)) {
          this.collection.push(object);
        }
        // this.center.copy(
        //   object.geometry.boundingSphere.center
        // );

        // this.center.applyMatrix4(object.matrixWorld);

        // if (frustum.containsPoint(this.center)) {
        //   this.collection.push(object);
        // }
      }
    }

    if (object.children && object.children.length > 0) {
      for (var x = 0; x < object.children.length; x++) {
        this.searchChildInFrustum(frustum, object.children[x]);
      }
    }
  }
}
